import React from 'react';
import {Link} from 'react-router-dom';
import { heroImg } from '../../helpers/heroImg';

//Si la carpeta assets esta en el Src
//const heroImg = require.context('../../assets/heroes', true);

export const HeroCard = ({ id, superhero, publisher, alter_ego, first_appearance, characters }) => {
    return (
        <div className="card ms-3" style={{ maxWidth: 540 }}>
            <div className="row no-gutters">
                <div className="col-md-4">
                    {/* <img src={`./assets/heroes/${id}.jpg`} className="card-img" alt={superhero}></img> */}
                    <img src={heroImg(`${id}.jpg`)} className="card-img" alt={superhero}></img>
                   
                </div>
                <div className="col-md-8">
                    <div className="card-body">
                        <h5 className="card-title">{superhero}</h5>
                        <p className="card-text">
                           {alter_ego} 
                        </p>
                        {
                            //Condicion
                            (alter_ego !== characters) 
                            && <p className="card-text">{characters}</p>
                        }
                        <p className="card-text">
                            <small className="text-muted">{first_appearance}</small>
                        </p>
                        <Link to={`./hero/${id}`}>
                        Mas...</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
